header {
    height: 100vh;
    padding-top:7rem;
    overflow: hidden;
}
.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display : flex ;
    gap : 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8 rem;
    position: absolute;
    left : 0;
    bottom: 3rem;
}   

.header__socials::after{
    content : '';
    width : 1px;
    height : 2rem ;
    background : var(--color-primary);
}

.scroll__down {
    position: absolute;
    right : -2.3rem;
    bottom : 5rem;
    transform: rotate(90deg);
    font-weight: 200;
}

.roles {
    padding-top: 5rem;
}